import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RecipeService } from '../recipe.service';
import { Subscription } from 'rxjs/Rx';
import { Recipe } from "../recipe";
import { FormArray, FormGroup, FormControl, Validators, FormBuilder} from '@angular/forms';

@Component({
  selector: 'app-recipe-edit',
  templateUrl: './recipe-edit.component.html'
})
export class RecipeEditComponent implements OnInit, OnDestroy{
 private subscription:Subscription;
 private recipeIndex:number;
 private recipe: Recipe;
 private isNew = true;
 recipeForm: FormGroup;

  constructor(private route: ActivatedRoute,
              private recipeService: RecipeService,
              private formBuilder: FormBuilder,
              private router:Router) { }

  ngOnInit() {
    this.subscription = this.route.params.subscribe(  //getting id of recipe selected 
      (params:any) => {
         if(params.hasOwnProperty('id')){
           this.isNew = false;
          this.recipeIndex = +params['id']; //+sign to convert params['id'] which is string to NUMBER
          this.recipe = this.recipeService.getRecipe(this.recipeIndex);
         }else{
          this.isNew = true;
          this.recipe = null;
         }
         this.initForm();
        }
    );
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

  private initForm(){
    let recipeName ='';
    let recipeImageUrl = '';
    let recipeContent = '';
    let recipeIngredients : FormArray = new FormArray([]);

    if(!this.isNew){ //i.e. existing recipe.Below code prepopulates the form
      if(this.recipe.hasOwnProperty('ingredients')){
      for (let i=0; i<this.recipe.ingredients.length; i++){
        recipeIngredients.push(
          new FormGroup({
            name: new FormControl(this.recipe.ingredients[i].name, Validators.required),
            amount: new FormControl(this.recipe.ingredients[i].amount, [Validators.required, Validators.pattern("\\d+")])
          })  //only digits for amount
          );
      }//end of for loop
      }// end of if "hasOwnProperty"
    recipeName = this.recipe.name;
    recipeImageUrl = this.recipe.imagePath;
    recipeContent = this.recipe.description;
    } //end of if

    this.recipeForm = this.formBuilder.group({ //below are the names of the controls that should be in html code too
      name: [recipeName, Validators.required],
      imagePath: [recipeImageUrl, Validators.required],
      description: [recipeContent, Validators.required],
      ingredients: recipeIngredients
    });
  } //end of initForm function

  onSubmit(){
    const newRecipe = this.recipeForm.value;
    if(this.isNew){
      this.recipeService.addRecipe(newRecipe);
    }else{
      this.recipeService.editRecipe(this.recipe, newRecipe);
    }
    this.navigateBack();
  }
  onCancel(){
    this.navigateBack();
  }
  private navigateBack(){
    this.router.navigate(['../']);
  }

  onAddItem(name:string, amount:string){
    (<FormArray>this.recipeForm.controls['ingredients']).push(
      new FormGroup({
          name: new FormControl(name, Validators.required),
          amount: new FormControl(amount, [Validators.required, Validators.pattern("\\d+")])
      })
    );
  }
  onRemoveItem(index:number){
    (<FormArray>this.recipeForm.controls['ingredients']).removeAt(index);
  }
}



